import React, { useEffect, useState } from 'react';
import PostCard from './PostCard';

interface Post {
  id: number;
  date: string;
  title: string;
  content: string;
}

interface PostListProps {
  style?: React.CSSProperties; // Dodano prop style
}

const PostList: React.FC<PostListProps> = ({ style }) => { // Akceptuj prop style
  const [posts, setPosts] = useState<Post[]>([]);

  useEffect(() => {
    fetch('/dane.json')
      .then(response => response.json())
      .then(data => {
        const processedData = data.map((post: Post) => ({
          ...post,
          content: post.content.replace(/\n/g, '<br>'), // Zamie \n na <br>
        }));
        setPosts(processedData);
      })
      .catch(error => console.error('Error fetching posts:', error));
  }, []);

  return (
    <div style={{ ...styles.container, ...style }}> {/* Zastosuj przekazany styl */}
      {posts.slice().reverse().map(post => ( // Odwr tablic postw
        <PostCard key={post.id} {...post} />
      ))}
    </div>
  );
};

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column' as 'column',
    alignItems: 'center', // Przywrcono 'center'
    justifyContent: 'center', // Przywrcono justifyContent
    minHeight: '100vh',
    padding: '20px',
    boxSizing: 'border-box' as 'border-box',
    marginLeft: '0', // Przesunicie o dodatkowe 10% w lewo
  },
};

export default PostList;
