import React, { useState, useEffect } from 'react'; // Import useState and useEffect
import './ThemeSwitcher.css'; // Import the new CSS file

interface ThemeSwitcherProps {
  onThemeChange: (color: string) => void;
}

const ThemeSwitcher: React.FC<ThemeSwitcherProps> = ({ onThemeChange }) => {
  const [isDarkMode, setIsDarkMode] = useState(false); // Start with light mode

  useEffect(() => {
    // Call onThemeChange whenever isDarkMode changes
    onThemeChange(isDarkMode ? '#2c3e50' : '#e9ebee');
  }, [isDarkMode, onThemeChange]);

  const handleToggleChange = () => {
    setIsDarkMode(prevMode => !prevMode);
  };

  return (
    <div className="theme-switcher-container">
      <label className="switch">
        <input type="checkbox" checked={isDarkMode} onChange={handleToggleChange} />
        <span className="slider round">
          <span className="icon sun" role="img" aria-label="Sun"></span>
          <span className="icon moon" role="img" aria-label="Moon"></span>
        </span>
      </label>
    </div>
  );
};

export default ThemeSwitcher;
