import React, { useState } from 'react'; // Import useState
import PostList from './components/PostList';
import LogoCard from './components/LogoCard';
import ThemeSwitcher from './components/ThemeSwitcher'; // Import ThemeSwitcher

function App() {
  const [backgroundColor, setBackgroundColor] = useState('#e9ebee'); // Stan dla koloru ta

  return (
    <div className="App" style={{ ...appStyles.container, backgroundColor: backgroundColor }}> {/* Uyj stanu dla ta */}
      <PostList style={{ marginRight: '10%' }} />
      <div style={appStyles.rightColumn}> {/* Nowy div dla LogoCard i ThemeSwitcher */}
        <LogoCard />
        <ThemeSwitcher onThemeChange={setBackgroundColor} /> {/* Przeka funkcj do zmiany ta */}
      </div>
    </div>
  );
}

const appStyles = {
  container: {
    display: 'flex',
    flexDirection: 'row' as 'row',
    justifyContent: 'center',
    alignItems: 'flex-start',
    padding: '20px',
    // backgroundColor: '#e9ebee', // Usunito stay kolor, teraz jest ze stanu
  },
  rightColumn: { // Styl dla prawej kolumny, aby uoy LogoCard i ThemeSwitcher w pionie
    display: 'flex',
    flexDirection: 'column' as 'column',
    alignItems: 'center', // Centruj elementy w kolumnie
  }
};

export default App;
